@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&subset=cyrillic);
.fb-delete-icon {
  cursor: pointer;
  border: none;
  position: relative;
  background: transparent;
  height: 12px;
  width: 10px;
  padding: 0;
  opacity: 0.3;
  overflow: hidden;
}

.fb-delete-icon:focus {
  outline: none;
}

.fb-delete-icon:hover {
  opacity: 1;
}

.fb-delete-icon:before,
.fb-delete-icon:after {
  content: '';
  position: absolute;
  height: 1px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background-color: #333;
}

.fb-delete-icon:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.fb-delete-icon:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.fb-draggable {
  cursor: -webkit-grab;
  cursor: grab;
}

.fb-draggable + .fb-draggable {
  margin-top: 8px;
}

.fb-draggable--is-dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
  opacity: .2;
}

.fb-plate {
  position: relative;
  font-size: 12px;
  padding: 10px 35px 10px 15px;
  background-color: #fff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .15);
}

.fb-plate__icon {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.fb-form-errors {
  font-size: 12px;
  color: #f00;
  margin-top: 10px;
}

.fb-form__input {
  font-size: 16px;
  font-weight: 100;
  width: 100%;
  border: none;
  padding: 10px 15px;
}

.fb-route-list {
  list-style: none;
}

.fb-routes-aside__list {
  padding-top: 20px;
}

.google-map {
  width: 100%;
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  color: black;
  font-family: 'Open Sans', sans-serif, Arial;
}

#root {
  display: flex;
  justify-content: center;
  width: 100%;
  min-width: 720px;
  min-height: 100%;
  padding: 60px 80px;
  background-color: #f8f5f9;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

main {
  width: 100%;
  display: flex;
  justify-content: center;
}

.app__map,
.app__aside {
  width: 50%;
}

.app__aside {
  padding: 0 20px;
}

